* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

.App {
    min-height: 100vh;
    background-image: linear-gradient(to right, #fa709a 0%, #fee140 100%);
}

.search {
    min-height: 20vh;
    display: flex;
    align-items: center;
    justify-content: center;
}

.bar {
    width: 50%;
    border: none;
    padding: 10px;
}

.button {
    background: #fa709a;
    border: none;
    padding: 10px 20px;
    color: white;
    margin: 10px;
}

.recipes {
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
}

.recipe {
    border-radius: 10px;
    box-shadow: 0px 5px 20px rgb(71, 71, 71);
    margin: 20px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    background: white;
    align-items: center;
    min-width: 40%;
    padding: 40px;
}

.img {
    border-radius: 50%;
    width: 200px;
    height: 200px;
    margin: 20px 0;
}

li {
    font-size: large;
}